.body{
    background-color: #1a111b;
}
.nav_container{
    display: flex;
    background-color: #1a111b;
    color: #fff;
    justify-content: space-between;
    margin-top: 20px;
    margin-left: 80px;
    margin-right: 80px;
}
#logo{
    /* background-color: rgb(126, 157, 58); */
    font-family: outfit;
    font-weight: 1000px;
}
.nav_links{
    width: 500px;
    /* background-color: rgb(126, 157, 58); */
    list-style: none;
    display: flex;
    justify-content: space-around;
}
.nav_links >*:hover{
    cursor: pointer;
    transition: 200ms;
    transform: translateY(-5px);
    border-bottom: 4px solid #9747FF;
}